
import { defineComponent } from 'vue';

import uid from 'uid';

import AtCheckbox from '../AtCheckbox/AtCheckbox.vue';

export default defineComponent({
  name: 'AtCheckboxGroup',
  components: {
    AtCheckbox,
  },
  model: {
    prop: 'checked',
    event: 'change',
  },
  props: {
    atId: {
      type: String,
      default: `at-${uid(6)}`,
    },
    label: {
      type: String,
      default: undefined,
    },
    options: {
      type: Array,
      required: true,
    },
    checked: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    column: {
      type: Boolean,
      default: false,
    },
    button: {
      type: [Boolean, Object],
      default: false,
    },
  },
  setup(props, context) {
    return {
      onChange(optionValue: string, checked: boolean) {
        const newChecked = [...props.checked];
        if (checked) {
          newChecked.push(optionValue);
        } else {
          newChecked.splice(newChecked.indexOf(optionValue), 1);
        }
        context.emit('update:checked', newChecked);
        context.emit('change', newChecked);
      },
    };
  },
});
