
import { defineComponent } from 'vue';

import uid from 'uid';

import AtRadio from '../AtRadio/AtRadio.vue';

export default defineComponent({
  name: 'AtRadioGroup',
  components: {
    AtRadio,
  },
  props: {
    atId: {
      type: String,
      default: `at-${uid(6)}`,
    },
    label: {
      type: String,
      default: undefined,
    },
    options: {
      type: Array,
      required: true,
    },
    checked: {
      type: [Boolean, String],
      default: undefined,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    column: {
      type: Boolean,
      default: false,
    },
    button: {
      type: [Boolean, Object],
      default: false,
    },
  },
  setup(props, context) {
    return {
      onChange(optionValue: string) {
        context.emit('update:checked', optionValue);
        context.emit('change', optionValue);
      },
    };
  },
});
