<template>
  <div
    class="at-checkbox-group at-group"
    :class="{
      'at-group--disabled': disabled,
      'at-group--column': column,
      [button.shape]: button && button.shape && button.shape !== 'rectangle',
      [button.size]: button && button.size && button.size !== 'normal'
    }">
    <div class="at-group__inner">
      <label v-if="label !== undefined" class="at-group__label">{{ label }}:</label>
      <at-checkbox
        v-for="(option, index) in options"
        :key="index"
        :name="atId"
        :class="{
          'at-button': button,
          'major': button && checked.includes(option.value),
          'minor': button && !checked.includes(option.value),
          'disabled': (button && disabled) || option.disabled
        }"
        :checked="checked.includes(option.value)"
        :disabled="disabled || option.disabled"
        :label="option.label"
        @change="
          (checked) => {
            onChange(option.value, checked);
          }
        " />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

import uid from 'uid';

import AtCheckbox from '../AtCheckbox/AtCheckbox.vue';

export default defineComponent({
  name: 'AtCheckboxGroup',
  components: {
    AtCheckbox,
  },
  model: {
    prop: 'checked',
    event: 'change',
  },
  props: {
    atId: {
      type: String,
      default: `at-${uid(6)}`,
    },
    label: {
      type: String,
      default: undefined,
    },
    options: {
      type: Array,
      required: true,
    },
    checked: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    column: {
      type: Boolean,
      default: false,
    },
    button: {
      type: [Boolean, Object],
      default: false,
    },
  },
  setup(props, context) {
    return {
      onChange(optionValue: string, checked: boolean) {
        const newChecked = [...props.checked];
        if (checked) {
          newChecked.push(optionValue);
        } else {
          newChecked.splice(newChecked.indexOf(optionValue), 1);
        }
        context.emit('update:checked', newChecked);
        context.emit('change', newChecked);
      },
    };
  },
});
</script>
